import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
//icons
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import './Product.css'

import { render } from '@testing-library/react';


const Products = (props) => {


        return ( 

        <Link to={props.id}>
        
              <Product id={props.id} className="product">
                    <Imagen><img src={props.img}/></Imagen>
                    <Title><h1>{props.title}</h1></Title>
                    <Description><p>{props.description}</p></Description>
                    <Price>{props.price ? <div><ShoppingCartIcon/> ${props.price} </div>:<div><h1><i>free</i></h1> </div>}</Price>
                  
                </Product>
            
        </Link>    
              
            
        

         );
    
}
 
export default Products;

const Product = styled.div`
width:74%;
min-height: 270px;
max-height: 270px;
margin:10px;
padding:15px 0 10px 0px;
display: inline-block;
background-color:#1b93c2;
border-radius:15px 15px 15px 15px;
border: solid #4FCDFF 2px;
border-style: dotted;


.easy{
    background-color: #908fbb;
    font-size:10px;
}
.medium{
    background-color: #4ea6a6;
    font-size:10px;  
}
.hard{
    background-color: #da6964;
}
@media (min-width: 768px) {  
    width: 45%;
}
@media (min-width: 968px) {  
    width: 20%;
}

`;
const Imagen = styled.div`
width:90%;
heigth:200.8px;
overflow: hidden;
margin: auto;


img{
    width:97%;
    heigth:100%;
    border-radius: 5px;
   
}
`;
const Title = styled.div`
width:70%;
margin:0px 0 0px 0;
padding: 10px 0 0 0;
background-color:;
height:50px;
margin: auto;
h1{

    color:#FA9C34;
    text-shadow: -3px 3px #AD5C02;
    font-family: "Press Start 2P";
    font-size: 100%;
        
}
`;
const Description = styled.div`

color:#fff;
height:40px;

`;
const Price = styled.div`
div{
    font-family: "Press Start 2P";
}
h1{
    i{
        color:#39db91;
        font-family: "Press Start 2P";
    }
    
    font-size:20px;
}
color:#39addb;
font-size:20px;
font-family: "Press Start 2P";
margin:0 0 10px 0;

`;
const Go = styled.div`
button{
    margin:10px 0 20px 0 ;
    outline: none;
background-color: #d19fcf;
color:#eaeaea;
border-radius: 5px;
font-size: large;
font-family: "Press Start 2P";
letter-spacing: 1.5px;
text-shadow: 2px -1px #5c553e;
padding: 10px;
box-shadow: -3px 5px #a55ba2;
border: none;
}
button:active{
    color: #d19fcf;
    background-color: #a55ba2;
    box-shadow: 0 2px #d19fcf;
}
`;